  display: flex;
}

.card__pic {
  background-image: url("imac.202c52ff.jpeg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__name {
  text-align: left;
  color: #060b35;
  width: 166px;
  height: 36px;
  margin-top: 40px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  text-align: left;
  color: #616070;
  width: auto;
  height: auto;
  margin-top: 4px;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  text-align: right;
  justify-content: space-between;
